.header {
   display: flex;
   justify-content: space-between;
   font-weight: 600;
   min-height: 56px;
   align-items: center;
   text-align-last: left;
   z-index: 2;
   background: transparent;
   border-radius: 28px;
   background: var(--accentColor);
   &__right {
      display: flex;
      align-items: center;
   }
   &__logo {
      height: 56px;
      font-weight: 900;
      font-size: 36px;
      display: flex;
      justify-content: center;
      align-items: center;
      color: var(--mainColor);
      border-radius: 32px;
      font-style: italic;
      aspect-ratio: 2;
   }
   &__social {
      width: 66px;
      height: 56px;
      display: flex;
      align-items: center;
      justify-content: center;
      cursor: pointer;
      position: relative;
      z-index: 2;
      color: var(--mainColor);
      border-radius: 50%;
      svg {
         width: 36px;
         height: 36px;
      }
   }
   &__mode-btn {
      height: 56px;
      width: 56px;
      display: flex;
      align-items: center;
      justify-content: center;
      border-radius: 50px;
      cursor: pointer;
      position: relative;
   }
   &__icon-mode {
      pointer-events: none;
      color: var(--mainColor);
      height: 36px;
      width: 36px;
      position: absolute;
      top: 10px;
      left: 10px;
      div {
         display: flex;
         justify-content: center;
         align-items: center;
      }
      svg {
         height: 36px;
         width: 36px;
      }
   }
}
.header__dark {
   opacity: 0;
   transition: 0.1s;
}
.header__light {
   opacity: 1;
   transition: 0.1s;
}
body.darkmode .header__dark {
   opacity: 1;
}
body.darkmode .header__light {
   opacity: 0;
}
