// import fonts/font <=>
@import url("https://fonts.googleapis.com/css2?family=Montserrat:ital,wght@0,100..900;1,100..900&display=swap");
// default font / default font size <=>
$fontFamily: "Montserrat", sans-serif;
$fontSize: 14;
//transition for dark mode and light
* {
   transition: color 0.2s;
   transition: background 0.2s;
}
// colors palette
:root {
   --black-p: #ebebf0;
   --black-s: #cacacf;
   --white-p: #0c0c0f;
   --white-s: #1a1a1b;
   --header: #6764642c;
   --transparent: #e0cdcd38;
}

//dark mode and light <=>
.darkmode {
   --black-p: #0c0c0f;
   --black-s: #1e1e22;
   --white-p: #ebebf0;
   --white-s: #cbcad0;
   --header: #5252531e;
   --transparent: #1f1d1d31;
}
// max width for container <=>
$maxWidthContainer: 1250;
// size gadjes for adapted and other <=>
$tablet: 991.98px;
$mobile: 767.98px;
$mobileSmall: 479.98px;
// null style <=>
@import "scss/_null.scss";
// general style <=>
body {
   line-height: 1.4;
   font-family: $fontFamily;
   font-size: $fontSize + px;
   animation: mainanim 0.7s ease-in-out;
   background: #000;
}
@keyframes mainanim {
   from {
      opacity: 0;
   }
   to {
      opacity: 1;
   }
}
// wrapper <=>
.wrapper {
   //max-width: $mobile;
   height: 100svh;
   width: 100%;
   margin: 0 auto;
   display: flex;
   justify-content: center;
   align-items: center;
   position: relative;
   overflow: hidden;
}
.player {
   min-height: 500px;
   min-width: 320px;
   max-width: 500px;
   width: 100%;
   height: 100%;
   gap: 4px;
   display: flex;
   flex-direction: column;
   justify-content: space-between;
   position: relative;
   padding: 4px;
}


// container <=>
.container {
   max-width: $maxWidthContainer + px;
   margin: 0 auto;
   //fix for slider
   > * {
      min-width: 0;
   }
}
main {
   padding-left: 80px;
   @media (max-width: $mobile) {
      padding-left: 0;
      padding-bottom: 150px;
   }
}

// scroll bar <=>
@media only screen and (max-width: $mobile) {
   ::-webkit-scrollbar {
      display: none;
   }
}
::-webkit-scrollbar {
   background-color: var(--white-s);
   width: 16px;
}
::-webkit-scrollbar-track {
   background-color: var(--white-s);
}
::-webkit-scrollbar-thumb {
   background-color: var(--black-p);
   border-radius: 16px;
   border: 4px solid var(--white-s);
}
::-webkit-scrollbar-thumb:hover {
   background-color: var(--black-s);
}
::-webkit-scrollbar-thumb:active {
   background-color: var(--black-p);
}
::-webkit-scrollbar-button {
   display: none;
}

// when copu text<=>
::selection {
   color: #101010;
   background: var(--mainColor);
}

// imports
@import "./scss/_header";
@import "./scss/_song-list";
@import "./scss/_song-item";
@import "./scss/controls";
@import "./scss/_footer";
@import "./scss/_loading";
