.song-item {
   transition: 0.5s;
   height: 100%;
   width: 100%;
   padding: 14px;

   &__wrapper {
      height: 100%;
      width: 100%;
      display: flex;
      justify-content: center;
      align-items: center;
      transition: 0.5s;
      position: relative;
   }
   &__img,
   &__img-bg {
      width: 100%;
      aspect-ratio: 1/1;
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      z-index: 5;
      user-select: none;
      border-radius: 30px;
      transition: 0.4s cubic-bezier(0.36, 0.07, 0.16, 1.09);
   }
   &.list-open .song-item__img {
      transform: translate(-50%, -50%) scale(0.8);
   }
   &__img-bg {
      object-fit: cover;
      z-index: 3;
      user-select: none;
      width: 150%;
      height: 100%;
      filter: blur(28px);
   }
   &__bg {
      aspect-ratio: 2/1;
      height: 100%;
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      user-select: none;
      z-index: 4;
      background: linear-gradient(#000, #00000000, #00000000, #000);
   }
}
.color-grid {
   display: grid;
   grid-template-columns: repeat(3, 1fr);
   width: 80%;
   height: 80%;
   border-radius: 20px;
   overflow: hidden;
   filter: blur(60px) contrast(110%);
   //opacity: 0.8;
   transform: scale(0.9);
   transition: 0.5s;
}
