.music-list {
   position: absolute;
   bottom: 0px;
   left: 0;
   width: 100%;
   height: 100%;
   flex: 1 1 auto;
   transition: 0.2s ease-in-out;
   z-index: 20;
   display: flex;
   align-items: flex-end;
   margin-bottom: 70px;
   padding: 18px;
   z-index: 100;
   user-select: none;
   pointer-events: none;
   &::-webkit-scrollbar {
      display: none;
   }
   &__body {
      width: 100%;
      max-height: 70%;
      background: #6060606c;
      position: relative;
      backdrop-filter: blur(25px);
      overflow: hidden;
      border-radius: 30px 30px 15px 30px;
      //border-radius: 15px;\
      opacity: 0;
      transition: 0.2s cubic-bezier(0.36, 0.07, 0.16, 1.09);
   }
   &__header {
      display: flex;
      justify-content: space-between;
      padding: 20px;
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      z-index: 6;
      color: var(--black-s);
      font-weight: 600;
      display: none;
   }
   &__row {
      display: flex;
      gap: 15px;
      align-items: center;
   }
   &__row span {
      padding-top: 2px;
   }
   &__list {
      display: flex;
      flex-direction: column;
      overflow-y: scroll;
      height: 50svh;
      //margin-top: 66px;
      mask-image: linear-gradient(
         to top,
         rgba(0, 0, 0, 0),
         #000,
         #000,
         #000,
         #000,
         #000,
         #000,
         #000,
         #000,
         #000,
         #000,
         #000,
         #000,
         #000,
         rgba(0, 0, 0, 0)
      );
      &::-webkit-scrollbar {
         display: none;
      }
   }
   &__line {
      display: flex;
      justify-content: space-between;
      padding: 15px 15px 15px 20px;
      cursor: pointer;
      transition: background 0.3s;
      color: var(--black-p);
   }
   &__line:hover {
      background: #ffffff10;
   }
   &__line.playing {
      transition: background 0.3s;
      background: #ffffff20;
   }
   &__item p,
   &__item span {
      color: var(--black-p);
      font-weight: 600;
   }
   &__item p {
      opacity: 0.8;
      font-size: 12px;
      letter-spacing: 1px;
   }
   &__icon {
      cursor: pointer;
      height: 25px;
      width: 25px;
      div {
         height: 25px;
         width: 25px;
      }
      svg {
         height: 25px;
         width: 25px;
      }
   }
   &__icon:nth-child(2) {
      height: 20px;
      width: 20px;
      div {
         height: 20px;
         width: 20px;
      }
      svg {
         height: 20px;
         width: 20px;
      }
   }
   &__info-item {
      background: var(--mainColor);
      height: 100%;
      aspect-ratio: 1.6/1;
      cursor: pointer;
      border-radius: 20px;
      color: var(--accentColor);
      font-weight: 600;
      transition: 0.3s;
      display: flex;
      justify-content: center;
      align-items: center;
      div {
         height: 20px;
         width: 20px;
      }
      svg {
         height: 20px;
         width: 20px;
         color: var(--accentColor);
      }
   }
}
.music-list.open {
   pointer-events: all;
}
.music-list.open .music-list__body {
   opacity: 1;
   transform: scale(1);
}
// aniamtion playing
.playing-animation {
   display: flex;
   align-items: flex-end;
   gap: 2px;
   transform: scale(0.7);
   &__bar {
      display: inline-block;
      background: var(--accentColor);
      width: 6px;
      height: 100%;
      border-radius: 2px;
      &1 {
         height: 60%;
      }
      &2 {
         height: 30%;
         animation-delay: -2.2s;
      }
      &3 {
         height: 75%;
         animation-delay: -3.7s;
      }
   }
}
.playing-animation.playing {
   .playing-animation__bar {
      animation: up-and-down 1.3s ease infinite alternate;
      &1 {
         height: 60%;
      }
      &2 {
         height: 30%;
         animation-delay: -2.2s;
      }
      &3 {
         height: 75%;
         animation-delay: -3.7s;
      }
   }
}

@keyframes up-and-down {
   20% {
      height: 30%;
   }

   40% {
      height: 90%;
   }

   60% {
      height: 50%;
   }

   80% {
      height: 75%;
   }

   100% {
      height: 60%;
   }
}
