/* src/components/LoadingAnimation.css */

.loading-animation {
   position: fixed;
   top: 0;
   left: 0;
   width: 100%;
   height: 100%;
   background-color: #000;
   display: flex;
   justify-content: center;
   align-items: center;
   opacity: 1;
   z-index: 9999999999999;
   transition: 2s 1s;
   &__logo {
      font-weight: 900;
      font-size: 70px;
      color: #fff;
      opacity: 0;
      animation: fade-in-out 2.8s ease forwards;
      text-align: center;
      position: relative;
      font-style: italic;
      &::after {
         content: '';
         width: 50px;
         height: 50px;
         border-radius: 50%;
         background: #fff;
         z-index: -1;
         position: absolute;
         top: 50%;
         left: 50%;
         transform: translate(-50%, -50%);
         filter: blur(60px);
         animation: fade-in-out-bg 2s 0.5s ease forwards;
      }
   }
}
@keyframes fade-in-out {
   0% {
      opacity: 0;
      transform: scale(0.8);
   }
   25% {
      opacity: 1;
      transform: scale(1);
   }
   75% {
      opacity: 1;
      transform: scale(1);
   }
   100% {
      opacity: 0;
      transform: scale(0.8);
   }
}
@keyframes fade-in-out-bg {
   0% {
      opacity: 0;
      transform: scale(0.8) translate(-50%, -50%);
   }
   25% {
      opacity: 1;
      transform: scale(1) translate(-50%, -50%);
   }
   75% {
      opacity: 1;
      transform: scale(1) translate(-50%, -50%);
   }
   100% {
      opacity: 0;
      transform: scale(0.8) translate(-50%, -50%);
   }
}

.loading-animation.loaded {
   opacity: 0;
}
