.controls {
   display: flex;
   flex-direction: column;
   gap: 20px;
   padding: 14px;
   position: relative;
   bottom: 0;
   width: 100%;
   left: 0;
   z-index: 100;
   &__progress {
      height: 6px;
      width: 100%;
      border-radius: 10px;
      background: #6060606c;
      cursor: pointer;
      user-select: all;
      pointer-events: all;
      z-index: 10;
      transition: 0.2s;
   }
   &.list-open &__progress {
      opacity: 0;
      transform: translateY(50px);
      pointer-events: none;
      visibility: hidden;
   }
   &__progress-bar {
      height: inherit;
      border-radius: 10px;
      width: 0%;
      position: relative;
      user-select: none;
      pointer-events: none;
      z-index: 10;
      transition: 0.1s;
   }
   &__progress-bar::after {
      content: "";
      position: absolute;
      left: 0;
      width: calc(100% + 10px);
      border-radius: 25px;
      background: var(--mainColor);
      height: 10px;
      top: -2px;
   }
   &__progress-bar span {
      content: "";
      position: absolute;
      border-radius: 25px;
      backdrop-filter: blur(3px);
      background: var(--transparent);
      width: 16px;
      height: 16px;
      top: -5px;
      right: -13px;
      z-index: 1;
      transition: 0.3s;
   }
   &__timer {
      display: flex;
      justify-content: space-between;
      padding: 5px;
      color: var(--mainColor);
      opacity: 0.8;
      user-select: none;
   }
   &__player-controls {
      display: flex;
      justify-content: space-between;
      align-items: center;
      gap: 10px;
      padding-top: 10px;
   }
   &__icon-mode,
   &__icon {
      width: 24px;
      height: 24px;
      color: var(--mainColor);
      cursor: pointer;
      transition: 0.5s;
      height: 45px;
      width: 45px;
      display: flex;
      justify-content: center;
      align-items: center;
      div {
         width: 24px;
         height: 24px;
         display: flex;
         justify-content: center;
         align-items: center;
      }
      svg {
         width: 24px;
         height: 24px;
      }
   }

   &__play-pause {
      padding: 10px 20px;
      background: var(--mainColor);
      border-radius: 30px;
      cursor: pointer;
      display: flex;
      justify-content: center;
      align-items: center;
      gap: 10px;
      transition: background 0.2s;
      height: 60px;
      min-width: 110px;
      z-index: 10;

      backdrop-filter: blur(25px);
   }
   &__play-pause.playing {
      background: #6060606c;
   }
   &__list {
      display: flex;
      justify-content: space-between;
      width: 100%;
      background: #6060606c;
      backdrop-filter: blur(25px);
      border-radius: 15px;
      height: 60px;
      padding: 8px;
      z-index: 10;
   }
   &__icon-play-pause {
      width: 28px;
      height: 28px;
      position: absolute;
      svg {
         width: 28px;
         height: 28px;
      }
   }
   &__play-pause.playing {
      .controls__icon-play-pause {
         color: var(--mainColor);
         svg {
            color: var(--mainColor);
         }
      }
   }
   &__play-pause {
      .controls__icon-play-pause {
         color: var(--accentColor);
         svg {
            color: var(--accentColor);
         }
      }
   }
   &__song-details {
      z-index: 2;
      transition: 0.2s;
      user-select: none;
   }
   &.list-open &__song-details {
      opacity: 0;
      transform: translateY(50px);
      pointer-events: none;
      visibility: hidden;
   }
   &__song-name {
      font-size: 18px;
      color: var(--mainColor);
      user-select: none;
   }
   &__author {
      font-size: 14px;
      opacity: 0.7;
      color: var(--mainColor);
      user-select: none;
   }
   &__icon-loading {
      opacity: 0;
      color: var(--white-p);
      position: absolute;
      transition: 0.2s;
      z-index: 7;
      animation: rotate 2s linear infinite;
      svg {
         color: var(--white-p);
      }
   }
}
@keyframes rotate {
   from {
      transform: rotate(0deg);
   }
   to {
      transform: rotate(360deg);
   }
}
.controls__play-pause.disabled {
   .controls__icon-loading {
      opacity: 1;
      visibility: visible;
   }
}
.controls__play-pause {
   .controls__icon-play-pause:nth-child(1) {
      opacity: 1;
      visibility: visible;
   }
   .controls__icon-play-pause:nth-child(2) {
      opacity: 0;
      visibility: hidden;
   }
   .controls__icon-loading {
      opacity: 0;
      visibility: hidden;
   }
}
.controls__play-pause.playing {
   .controls__icon-play-pause:nth-child(1) {
      opacity: 0;
      visibility: hidden;
   }
   .controls__icon-play-pause:nth-child(2) {
      opacity: 1;
      visibility: visible;
   }
}

.repeat__btns svg {
   display: none;
   pointer-events: none;
}

.repeat__btns.repeat svg:nth-of-type(1) {
   display: block;
}
.repeat__btns.repeatOne svg:nth-of-type(2) {
   display: block;
}
.repeat__btns.shuffle svg:nth-of-type(3) {
   display: block;
}
